.App {
  font-family: sans-serif;
  text-align: center;
}
.fontBox {
  font-family: "Mali", cursive;
  font-size: 16px;
}
.backgroundBox {
  background-color: #fdfdf7;
}
.TitleStyle {
  font-family: "Mali", cursive;
  font-size: 15px;
  font-weight: 580;
  background: #ffffff;
  color: rgb(90, 90, 90);
  border: none;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
}
.buttonStyle {
  color: gray;
  border: none;
  background-color: transparent;
  padding: 8px;
  font-family: "Mali", cursive;
  font-size: 12px;
}
.Space {
  height: 12px;
}

.Space1 {
  height: 30px;
}
.Space2 {
  height: 100px;
}
.FontHead {
  text-align: center;
  font-weight: bold;
  font-size: 30px;
  color: rgb(116, 116, 116);
}

.FontHead1 {
  font-family: "IBM Plex Sans Thai", sans-serif;
  text-align: center;
  font-weight: bold;
  font-size: 25px;
  color: rgb(255, 255, 255);
}
.FontHead2 {
  font-family: "Pattaya", sans-serif;
  text-align: center;
  font-weight: bold;
  font-size: 35px;
  color: rgb(255, 255, 255);
}
.FontHead3 {
  font-family: "IBM Plex Sans Thai", sans-serif;
  text-align: center;
  font-size: 17px;
  color: rgb(255, 255, 255);
  line-height: 2.5;
}
.HomePage {
  font-family: "Mali", cursive;
  font-size: 15px;
  font-weight: 580;
  color: rgb(243, 234, 234);
  border: none;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  background: linear-gradient(to bottom right, #040122 59%, #4024a5 120%);
  background-size: cover;
  line-height: 2;
}

.buttonStyle1 {
  font-family: "Mali", cursive;
  font-size: 15px;
  color: rgb(255, 255, 255);
  background: transparent;
  border: none;
  animation: blink 1.5s step-start infinite;
  text-decoration: none;
  display: inline-block;
  background-color: Transparent;
  background-repeat: no-repeat;
  cursor: pointer;
  overflow: hidden;
  outline: none;
}
.buttonStyle2 {
  font-family: "Mali", cursive;
  font-size: 15px;
  color: rgb(75, 72, 72);
  background: transparent;
  border: none;
  animation: blink 1.5s step-start infinite;
}
@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.fontPage {
  font-family: "Mali", cursive;
  font-size: 15px;
  line-height: 1.6;
  padding: 20px;
  color: rgb(66, 66, 66);
}
.fonHeader {
  font-family: "Mali", cursive;
  font-size: 20px;
  line-height: 1.2;
  font-weight: bold;
  padding: 10px;
  text-align: center;
}
.fonHeader1 {
  font-family: "Mali", cursive;
  font-size: 17px;
  line-height: 1.6;
  font-weight: bold;
  padding: 10px;
  text-align: center;
}
.center {
  text-align: center;
}
.right {
  text-align: right;
  font-size: 12px;
  color: rgb(179, 179, 179);
}
